#header-ul {
  display: grid;
  grid-template-columns: 2fr 1fr 0.25fr 0.4fr;
  list-style-type: none;
}

#icon {
  grid-column: 1/2;
  justify-self: start;
  margin-left: 3em;
  color: #ff4356;
  font-weight: bold;
}

#link1 {
  grid-column: 2/3;
  justify-self: end;
  color: white;
}

#link2 {
  color: white;
  justify-self: end;
}

#link3 {
  color: white;
}

@media screen and (max-width: 800px) {
  #header-ul {
    grid-template-columns: 2fr 1fr 0.9fr 1fr;
  }
}
