#Dodge {
  display: grid;
  grid-template-rows: 3em 1fr 8em 15em 6em;
}

h4,
h5 {
  grid-row: 2/3;
  color: #ff4356;
}

h4 {
  font-size: 7em;
}

h5 {
  font-size: 1.3em;
}

form {
  margin-top: 1em;
  align-self: center;
}

.server-selection {
  margin-top: 0.4em;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.server-selection li {
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 10px;
  background-color: #ff4356;
  width: 3.5em;
  height: 1.8em;
}

.buscador {
  border: none;
  width: 40em;
  height: 2.5em;
  border-radius: 2px;
}

.boton {
  border: none;
  background-color: #ff4356;
  width: 4em;
  height: 2.5em;
  margin: 0 5px;
  border-radius: 2px;
  color: white;
  font-weight: bold;
}

.dodge-main {
  align-self: center;
}

.usersd-section {
  display: flex;
  justify-content: space-evenly;
}

.advice {
  color: #ff4356;
  font-size: 2em;
  margin-top: 1em;
}

@media screen and (max-width: 800px) {
  #Dodge {
    display: grid;
    grid-template-rows: 3em 1fr 1fr auto 8em;
  }

  h4 {
    font-size: 5em;
  }
  h5 {
    font-size: 1em;
  }
  .buscador {
    width: 17em;
  }
  .usersd-section {
    flex-direction: column;
    align-items: center;
  }
}
