#Start {
  display: grid;
  grid-template-rows: 3em 20em 10em 5em;
}

header {
  grid-row: 1/2;
  margin-top: 1em;
}
h1,
h2 {
  grid-row: 2/3;
  color: #ff4356;
}

h1 {
  font-size: 7em;
}

.title {
  align-self: center;
}

#start-ul {
  grid-row: 3/4;
  display: flex;
  align-items: center;
  justify-content: center;
  list-style-type: none;
}

#start-ul a {
  text-decoration: none;
}

#start-ul li {
  display: flex;
  width: 8em;
  height: 2.3em;
  margin: 0 20px;
  background-color: #ff4356;
  color: white;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
}

footer {
  align-self: center;
}

@media screen and (max-width: 800px) {
  #Start {
    grid-template-rows: 3em 20em 15em 5em;
  }

  #start-ul {
    flex-direction: column;
  }
  #start-ul li {
    margin: 10px 0;
  }
  h1 {
    font-size: 5em;
  }
  h2 {
    font-size: 1.3em;
  }
}
